<script>
import { mapGetters } from 'vuex';

import { Banner } from '@components/Banner';
import Loading from '@shell/components/Loading';

import PolicyList from '../components/Policies/PolicyList';

export default {
  components: {
    Banner, Loading, PolicyList
  },

  props: {
    resource: {
      type:     String,
      required: true,
    },
    schema: {
      type:     Object,
      required: true,
    },
  },

  async fetch() {
    await this.$store.dispatch(`${ this.currentProduct.inStore }/findAll`, { type: this.resource });
  },

  computed: {
    ...mapGetters(['currentProduct']),

    rows() {
      return this.$store.getters[`${ this.currentProduct.inStore }/all`](this.resource);
    }
  }
};
</script>

<template>
  <Loading v-if="$fetchState.pending" />
  <div v-else>
    <Banner
      class="type-banner mb-20 mt-0"
      color="info"
      :label="t('kubewarden.admissionPolicy.description')"
    />

    <PolicyList :resource="resource" :rows="rows" :schema="schema" />
  </div>
</template>
